<template>
  <div align="left" style="min-height: 100px">
    <code-mirror basic v-if="type === 'json'" :lang="langJson" v-model="codeVal"/>
    <code-mirror basic v-if="type === 'xml'" :lang="langXml" v-model="codeVal"/>
    <code-mirror basic v-if="type === 'html'" :lang="langHtml" v-model="codeVal"/>
  </div>
</template>

<script setup>
import {onBeforeUnmount, onMounted, ref} from 'vue'
import CodeMirror from 'vue-codemirror6'
import {json} from '@codemirror/lang-json';
import {xml} from '@codemirror/lang-xml';
import {html} from '@codemirror/lang-html';
// json
const langJson = json();
const langXml = xml();
const langHtml = html();
// 初始化
let codeVal = ref('');
const type = ref('json')
// 定义处理JavaFX消息的函数
const handleJavaFXMessage = (event) => {
  const messageData = event.detail;
  console.log('Received message from JavaFX:', messageData)
  try {
    const data = JSON.parse(messageData);
    // 转成json字符串并格式化
    codeVal.value = data.data
    type.value = data.langType
  } catch (error) {
    console.error('Failed to parse JSON:', error);
  }
};
/*var event = new CustomEvent('messageFromJavaFX', {
  detail: JSON.stringify({
    data:'<Document>\n' +
        '<Response>\n' +
        '<ResponseCode>0</ResponseCode>\n' +
        '<ResponseInfo>成功！</ResponseInfo>\n' +
        '<Package>44$$All4$$1759170600</Package>\n' +
        '<MacExpireTime>不限</MacExpireTime>\n' +
        '<Status>1</Status>\n' +
        '<Created>2024-09-30 06:44:55</Created>\n' +
        '</Response>\n' +
        '</Document>',
    langType:"xml"
  })
});

setTimeout(() => window.dispatchEvent(event), 100)*/
// 在组件挂载时添加事件监听器
onMounted(() => {
  // 确保DOM已经完全加载
  window.addEventListener('messageFromJavaFX', handleJavaFXMessage);
});

// 在组件卸载前移除事件监听器
onBeforeUnmount(() => {
  window.removeEventListener('messageFromJavaFX', handleJavaFXMessage);
});
</script>
<style>
.cm-scroller{
  min-height: 100px;
}
.CodeMirror pre.CodeMirror-line {
  white-space: pre-wrap;
}

</style>
